import React from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';

import { Button } from 'antd';
import { WeaTableNew } from 'comsMobx';
import { WeaTop, WeaTab, WeaRightMenu, WeaLeftRightLayout, WeaLeftTree } from 'ecCom';

import { renderNoright, getSearchs } from '../util'; // formutil

const WeaTable = WeaTableNew.WeaTable;

@inject('baseTableStore')
@observer
export default class BaseOrgTable extends React.Component {
  componentWillMount() { // 
    const { baseTableStore: { doInit } } = this.props;
    doInit();
  }

  componentWillReceiveProps(nextProps) {
    const { baseTableStore } = this.props;
    if (this.props.location.key !== nextProps.location.key) { //  
      baseTableStore.doInit();
    }
  }

  showColumn = () => {
    const { baseTableStore: { tableStore } } = this.props;
    tableStore.setColSetVisible(true);
    tableStore.tableColSet(true);
  }
  
  render() {
    const { baseTableStore } = this.props;
    const { loading, hasRight, form, condition, tableStore, showSearchAd, treeDatas,
      getTableDatas, doSearch, setShowSearchAd } = baseTableStore;

    if (!hasRight && !loading) { // 
      return renderNoright();
    }

    const rightMenu = [// 
      {
        key: 'BTN_COLUMN',
        icon: <i className='icon-coms-Custom'/>,
        content : '',
        onClick : this.showColumn
      },
    ];
    const collectParams = { // 
      favname: '-',
      favouritetype: 1,
      objid: 0,
      link: 'wui/index.html#/ns_demo05/index',
      importantlevel: 1,
    };
    const adBtn = [ // 
      <Button type="primary" onClick={doSearch}></Button>,
      <Button type="ghost" onClick={() => form.resetForm()}></Button>,
      <Button type="ghost" onClick={() => setShowSearchAd(false)}></Button>,
    ];

    return (
      <WeaRightMenu
        datas={rightMenu} // 
        collectParams={collectParams} // :   3
      >
        <WeaTop
          title="-" // 
          icon={<i className='icon-coms-meeting' />} // 
          iconBgcolor='#F14A2D' // 
          showDropIcon={true} // 
          dropMenuDatas={rightMenu} // 
          dropMenuProps={{ collectParams }} // :   3
          loading={loading} // 
        >
          <WeaLeftRightLayout // 
            leftCom={(
              <WeaLeftTree // 
                datas={toJS(treeDatas)} // 	
                selectedKeys={form.getFormParams().type !== '' ? form.getFormParams().type : 'undefind'} // key
                onSelect={key => {
                  // 
                  form.updateFields({ type: { value: key } });
                  getTableDatas({
                    ...form.getFormParams(),
                    type: key,
                  });
                }} // 
              />
            )}
          >
            <WeaTab
              searchType={['base', 'advanced']} // base advanced
              showSearchAd={showSearchAd} // 
              setShowSearchAd={bool => setShowSearchAd(bool)} //
              searchsAd={getSearchs(form, toJS(condition), 2)} // 
              buttonsAd={adBtn} // 
              onSearch={getTableDatas} // 
              onSearchChange={v => form.updateFields({ requestname: v })} // : 
              searchsBaseValue={form.getFormParams().requestname} // input: requestname
            />
            <WeaTable // tableloading
              comsWeaTableStore={tableStore} // table store
              hasOrder={true} // 
              needScroll={true} // table
            />
          </WeaLeftRightLayout>
        </WeaTop>
      </WeaRightMenu>
    )
  }
}


